export interface ActionProps {
  type?: string;
  tabType?: string;
}

const reducer = <T extends ActionProps>(state: T, action: T): T => {
  const { type, tabType } = action;
  switch (type) {
    case 'setTabType':
      return {
        ...state,
        tabType,
      };
    default:
      return state;
  }
};

export default reducer;
